.stars {
  display: flex;
  width: 96px;
  justify-content: space-between;

  &__star {
    background-image: url(../images/star.svg);
    height: 16px;
    width: 16px;
    display: block;
    background-repeat: no-repeat;
    background-position: center;
  }
}


.stars--4 :nth-child(-n + 4) {
  background-image: url(../images/star-active.svg);
}
